@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  src: local("PT Mono"), url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: auto;
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: local("Roboto"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-display: auto;
}
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: auto;
}
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: auto;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  pointer-events: none;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  max-width: 1920px;
  margin: 0 18%;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
    margin: 0;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}
@media (max-width: 319px) {
  .container {
    padding: 0 16px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 146px 56px 10px 40px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px 40px 45px;
  background-color: #00b3ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__title {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-size: 30px;
  color: #ffffff;
  margin: 0 0 8px;
}
.modal__description {
  color: #ffffff;
  font-size: 16px;
  line-height: 1.4em;
  margin: 0 0 37px;
  width: 90%;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal form {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
}
.modal input, .modal textarea {
  width: 100%;
  border: none;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px 15px 17px;
  color: #ffffff;
}
.modal input:hover, .modal textarea:hover {
  box-shadow: inset 0 0 0 2px #f8fdff;
}
.modal input::placeholder, .modal textarea::placeholder {
  color: #ffffff;
}
.modal input:focus, .modal textarea:focus {
  outline: 2px solid #4169e1;
}
.modal textarea {
  padding: 25px 15px 20px;
}
.modal button {
  font-family: "Roboto Condensed";
  font-weight: 700;
  background-color: #041427;
  color: #ffffff;
  border: none;
  padding: 15px 15px 12px;
  border-radius: 3px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  cursor: pointer;
}
.modal button:hover {
  background-color: #f8fdff;
  color: #041427;
}
.modal .modal__close-btn {
  position: absolute;
  top: 15px;
  right: 14px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}
.modal .modal__close-btn:hover::before, .modal .modal__close-btn:hover::after {
  background-color: #041427;
}
.modal .modal__close-btn::before, .modal .modal__close-btn::after {
  content: "";
  display: block;
  width: 28px;
  height: 1px;
  border-radius: 1px;
  background-color: #ffffff;
  position: absolute;
  top: calc(50% - 1px);
  left: calc(50% - 14px);
}
.modal .modal__close-btn::before {
  transform: rotate(45deg);
}
.modal .modal__close-btn::after {
  transform: rotate(-45deg);
}

